import urlcat from "urlcat";
import {BASE_URL, PORT} from "@shared/utils/constant-server";
import {NOOP} from "@shared/utils/core";

const baseUrl: string = urlcat(`http://localhost:${PORT}`, BASE_URL);

export function postJson(url: string, data: any): Promise<void> {
  return window.fetch(urlcat(baseUrl, url), {
    method: 'POST',
    headers: {'Content-Type': 'application/json'},
    body: JSON.stringify(data),
  }).then(NOOP, NOOP);
}

