import {waitAMomentOnTruth, waitForAvailable, waitForSelected} from "@shared/utils/wait";
import {postJson} from "@/utils/request";

const first_td_anchor = '#table_wrapper-table .mywidth a[href*="//quote.eastmoney.com/unify/r/"]';
const thead_td_list_all = '#table_wrapper-table thead th';
const tbody_tr_list_all = '#table_wrapper-table tbody tr';
const page_next_jumper = '#table_wrapper .next.paginate_button';
const page_latest_jumper = '#main-table_paginate .paginate_page a:last-child';

function toClearText(content?: string | null): string {
  return (content || '').replace(/&\w{4};/g, '').trim();
}

async function waitFirstTdAvailable(
  hasHref: (href: string) => (boolean | Promise<void>)
): Promise<true | void> {
  return waitForAvailable(async () => {
    const firstTd = $(first_td_anchor).get(0) as HTMLAnchorElement | null;
    const href = firstTd?.href;
    if (href) {
      return !hasHref(href);
    }
  });
}

function readHeads() {
  const heads: string[] = [];
  $(thead_td_list_all).each(function () {
    heads.push(toClearText(this.textContent));
  });
  return heads;
}

function readRecords(heads: string[]): Record<string, string>[] {
  const recordsList: Record<string, string>[] = [];
  const trList = $(tbody_tr_list_all);

  console.log('trList: ', trList.length);

  trList.each(function () {
    const record: Record<string, string> = {};
    $(this).find('td').each(function (idx) {
      const anchors = $(this).find("a");
      if (anchors.length) {
        const links: any[] = [];
        anchors.map(function () {
          const text = toClearText(this.textContent);
          if (text) {
            links.push({href: this.href, text});
          }
        });
        if (links.length) {
          record[heads[idx]] = links as any;
        }
      } else {
        record[heads[idx]] = toClearText(this.textContent);
      }
    });
    recordsList.push(record);
  });
  return recordsList;
}

async function jumpToNextPage() {
  const jumper = await waitForSelected(page_next_jumper) as HTMLAnchorElement;
  jumper.click();
  return waitAMomentOnTruth();
}

function wasLatestPage(): boolean {
  return $(page_latest_jumper).hasClass('current')
}

interface StockInfo {
  code: string;
  href: string;
}

interface TypeRefs {
  index: number;
  max: number;
  map: Map<string | undefined, StockInfo>
}

async function readAll(refs: TypeRefs): Promise<any> {
  await waitFirstTdAvailable(href => refs.map.has(href));
  console.log($(first_td_anchor).length);

  const heads = readHeads();

  const records: Record<string, string>[] = readRecords(heads);

  $(first_td_anchor).each(function () {
    const href = $(this).attr('href') || '';
    const code = href.slice(href.lastIndexOf('.') + 1);
    if (href && code) {
      const info: StockInfo = {code, href};
      refs.map.set(href, info);
      refs.map.set(code, info);
    }
  });

  refs.index++;

  const end = wasLatestPage() || refs.index >= refs.max;
  await postJson('/fine/code/push/closed-gridlist', {
    index: refs.index,
    list: records,
    end,
  });
  if (!end) {
    return await jumpToNextPage().then(() => readAll(refs));
  }
}

function onReady() {
  $(async () => {
    $('#body-main').on('click', (e) => {
      e.stopPropagation();
    });

    const refs: TypeRefs = {
      index: 0,
      max: 1000,
      map: new Map(),
    };

    await readAll(refs);
  });
}

function waiting() {
  if (typeof $ === "function") {
    onReady();
  } else {
    setTimeout(waiting, 333);
  }
}

setTimeout(waiting);