import {readTextContent, selectBy} from './dom';
import {randomInt} from './number';
import {logger} from './log';
import {cacheable} from './cacheable';

export function waitForAvailable<T = any>(
  tester: () => (T | Promise<T>),
  timeout: number = -1,
): Promise<Exclude<T, null | undefined | false | '' | 0>> {
  return new Promise((resolve, reject) => {
    const endAt = timeout > 0 ? Date.now() + timeout : -1;

    function nextTask() {
      setTimeout(test);
    }

    function onValue(value: any) {
      if (value != null && value !== false) {
        resolve(value as any);
      } else if (endAt < 0 || Date.now() < endAt) {
        nextTask();
      } else {
        reject('Timeout');
      }
    }

    function test() {
      const value: any = tester();
      if (typeof value?.then === 'function') {
        value.then(onValue, nextTask);
      } else {
        onValue(value);
      }
    }

    test();
  });
}

export function waitForSelected(selector: string, timeout?: number) {
  return waitForAvailable(() => selectBy(selector), timeout);
}

export function waitForHasTextContent(
  selector: string,
  timeout?: number,
): Promise<string> {
  return waitForAvailable(() => readTextContent(selector)?.trim() || null, timeout);
}

export async function waitForTimeout(minTimeout: number, maxTimeout: number = minTimeout): Promise<void> {
  return new Promise((resolve) => {
    const timeout = maxTimeout - minTimeout > 16 ? randomInt(minTimeout, maxTimeout) : 16;
    // logger.trace('Waiting for timeout {} ms ...', timeout);
    setTimeout(resolve, timeout);
  });
}

const IntervalBased = cacheable((second: number): [number, number] => {
  const middle = second * 1000;
  const min = Math.ceil(second / 5) * 1000;
  const max = 2 * middle - min;
  return [min, max];
});

export async function waitAMomentOnTruth(isTruth: any = true, delaySeconds: number = 5): Promise<void> {
  if (isTruth) {
    let time = Math.max(1, delaySeconds);
    time = Number.isNaN(time) ? 5 : time;
    const [min, max] = IntervalBased(time);
    return waitForTimeout(min, max);
  }
}