export interface Selective {
  querySelector<E extends Element = Element>(selector: string): E | null
  querySelectorAll<E extends Element = Element>(selector: string): NodeListOf<E>,
}


export function selectBy(
  selector: string,
  from: Selective = document
) {
  return from.querySelector(selector);
}

export function selectAll(
  selector: string,
  from: Selective = document
) {
  return from.querySelectorAll(selector);
}

export function readTextContent(selector: string): string | null | undefined {
  return selectBy(selector)?.textContent;
}
