export function cacheable<K extends string | number, V>(
  resolver: (key: K) => V
): (key: K) => V {
  const caches: Record<number | string, any> = {};
  return (k: K): V => {
    let value = caches[k];
    if (value) {
      return value;
    }
    value = resolver(k);
    caches[k] = value;
    return value;
  }
}